import { useEffect, useLayoutEffect } from 'react';
import { LifeHook } from '../common/type';

/**  ngOnInit */
export const useBeforeRender: LifeHook = (fn) => {
    useLayoutEffect(() => {
        fn();
    }, []);
};

/**  ngAfterViewInit */
export const useAfterRender: LifeHook = (fn) => {
    useEffect(() => {
        fn();
    }, []);
};

/**  ngAfterViewInit */
export const useBeforeDestroy: LifeHook = (fn) => {
    useEffect(() => {
        return () => {
            fn();
        };
    }, []);
};
